export namespace LeUIHtml {
  export interface LayerTreeNode {
    layer: Layer;
    children: LayerTreeNode[];
  }
  export interface Artboard {
    id: string;
    layers: Layer[];
    treeLayers: LayerTreeNode[];
    flatTreeLayers: LayerTreeNode[];
  }
  export enum LayerType {
    container = "container", // 
    element = "element", // 
    list = "list", // 
    picture = "picture", // 
    text = "text" // content
  }
  export interface Layer {
    merged?: boolean;
    id: string;
    title: string;
    content: string;
    type: LayerType;
    size: {
      width: number;
      height: number;
    };
    percentageSize: {
      width: number;
      height: number;
    };
    // 
    location: {
      top: number;
      left: number;
    };
    codeTemplate: {
      css: {
        codes: { property: string; value: string }[];
      };
    };
  }
}
